import {
    Component,
    Host,
    Prop,
    h,
    Element,
    EventEmitter,
    Event,
} from '@stencil/core';
import { cva } from 'class-variance-authority';
import { AtuiEvent } from '../../../types/events';

export type TreeItemSize = 'sm' | 'md' | 'lg';

const variants = cva(
    'group text-button text-foreground hover:bg-surface-1 focus-visible:ring-active-foreground/30 relative box-border flex inline-flex cursor-pointer items-center justify-center gap-4 overflow-hidden rounded-[0.3rem] bg-transparent font-medium whitespace-nowrap capitalize duration-150 ease-in-out outline-none group-focus:bg-slate-200 focus:outline-none focus-visible:ring-2',
    {
        variants: {
            disabled: {
                false: null,
                true: 'pointer-events-none opacity-30 grayscale-[1]',
            },
            size: {
                sm: 'px-4 py-4',
                md: 'px-8 py-[6px]',
                lg: 'px-8 py-8',
            },
        },
    },
);

/**
 * @slot - Placed after the label, content is positioned at far right.
 */
@Component({
    tag: 'atui-tree-item',
    shadow: false,
})
export class AtuiTreeItemComponent {
    /**
     * Size of the button
     */
    @Prop()
    size: TreeItemSize = 'lg';

    /**
     * Label to be displayed within the button
     */
    @Prop()
    label!: string;

    /**
     * Defines the indent of the tree item
     */
    @Prop()
    depth?: number;

    /**
     * Determines whether to add the arrow indicator icon
     */
    @Prop()
    has_children?: boolean = false;

    /**
     * Determines the direction of the indicator arrow
     */
    @Prop({ mutable: true })
    selected?: boolean = false;

    /**
     * When set the button's styling will change and will no longer be interactive
     */
    @Prop({ mutable: true })
    disabled?: boolean = false;

    @Element()
    el: HTMLAtuiTreeItemElement;

    /**
     * Emits when the button is clicked
     */
    @Event()
    atuiClick: EventEmitter<AtuiEvent>;

    handleClick(event: Event): void {
        this.atuiClick.emit({
            originalEvent: event,
            componentType: 'atui-tree-item',
            element: this.el,
        });
    }

    handleKeyDown(event: KeyboardEvent): void {
        if (event.key === ' ' || event.key === 'Enter') {
            event.preventDefault();
            this.atuiClick.emit({
                originalEvent: event,
                componentType: 'atui-tree-item',
                element: this.el,
            });
        }
    }

    render() {
        const classname = variants({
            disabled: this.disabled,
            size: this.size,
        });

        const depth = this.depth ?? 0;
        const hasChildren = !!this.has_children;

        const indent = hasChildren ? `${depth * 20}px` : `${depth * 20 + 20}px`;

        return (
            <Host
                class={classname}
                role="button"
                tabIndex={0}
                aria-disabled={this.disabled}
                onKeyDown={(event) => this.handleKeyDown(event)}
                onClick={(event) => this.handleClick(event)}
            >
                <div
                    class="z-20 flex h-full w-full items-center justify-between gap-4"
                    style={{ paddingLeft: indent }}
                >
                    <div class="flex gap-4">
                        {this.has_children && (
                            <span
                                class="material-icons h-16 w-16 text-[16px] leading-[16px]"
                                data-name="tree-item-indicator"
                            >
                                {this.selected
                                    ? 'keyboard_arrow_down'
                                    : 'keyboard_arrow_right'}
                            </span>
                        )}

                        {this.label && (
                            <span
                                class="leading-[16px]"
                                data-name="tree-item-label"
                            >
                                {this.label}
                            </span>
                        )}
                    </div>
                    <slot />
                </div>

                <div
                    data-name="focus-indicator"
                    role="presentation"
                    class="pointer-events-none absolute top-0 left-0 z-10 h-full w-full transition-colors duration-300 ease-in-out"
                ></div>
            </Host>
        );
    }
}
